/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Helvetica Neue', Arial, sans-serif;
  }
  
  /* Page Wrapper */
  .ppc-page {
    background-color: #f7f7f7;
    color: #333;
  }
  
  /* Hero Section */
  .hero {
    background: url('https://example.com/hero-image.jpg') no-repeat center center/cover;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    padding: 20px;
  }
  
  .hero-content {
    max-width: 800px;
  }
  
  .hero-title {
    font-size: 4rem;
    font-weight: bold;
    line-height: 1.2;
    margin-bottom: 20px;
    text-transform: uppercase;
  }
  
  .hero-description {
    font-size: 1.6rem;
    margin-bottom: 40px;
  }
  
  .cta-button {
    background-color: #ff6f61;
    color: #fff;
    font-size: 1.6rem;
    padding: 15px 50px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .cta-button:hover {
    background-color: #e15b50;
    transform: scale(1.05);
  }
  
  /* Expertise Section */
  .expertise {
    padding: 60px 20px;
    background-color: #fff;
    text-align: center;
  }
  
  .expertise .section-title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
  }
  
  .expertise .section-description {
    font-size: 1.2rem;
    margin-bottom: 40px;
  }
  
  .expertise-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
  }
  
  .expertise-item {
    background-color: #f9f9f9;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }
  
  .expertise-item:hover {
    transform: translateY(-10px);
    box-shadow: 0 12px 25px rgba(0, 0, 0, 0.2);
  }
  
  .expertise-item h3 {
    font-size: 1.8rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .expertise-item p {
    font-size: 1.2rem;
  }
  
  /* Case Studies */
  .case-studies {
    padding: 60px 20px;
    background-color: #fafafa;
    text-align: center;
  }
  
  .case-studies .section-title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 40px;
    color: #333;
  }
  
  .cases {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  
  .case {
    background-color: #fff;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  
  .case h3 {
    font-size: 1.8rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .case p {
    font-size: 1.2rem;
  }
  
  /* Testimonials Section */
  .testimonials {
    padding: 60px 20px;
    background-color: #fff;
    text-align: center;
  }
  
  .testimonials .section-title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 40px;
    color: #333;
  }
  
  .testimonial-cards {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }
  
  .testimonial-card {
    background-color: #f4f4f4;
    padding: 40px;
    flex: 1;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  
  .testimonial-card p {
    font-size: 1.4rem;
    font-style: italic;
    margin-bottom: 20px;
  }
  
  .testimonial-card h4 {
    font-size: 1.2rem;
    font-weight: bold;
    color: #333;
  }
  
  /* Call to Action */
  .cta {
    background-color: #ff6f61;
    color: #fff;
    padding: 60px 20px;
    text-align: center;
  }
  
  .cta-title {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .cta-description {
    font-size: 1.4rem;
    margin-bottom: 40px;
  }
  
  .cta-button {
    background-color: #fff;
    color: #ff6f61;
    padding: 20px 50px;
    font-size: 1.6rem;
    border: 2px solid #ff6f61;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.3s ease;
  }
  
  .cta-button:hover {
    background-color: #ff6f61;
    color: #fff;
    transform: scale(1.05);
  }
  
  /* Modal */
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background-color: #fff;
    padding: 40px;
    width: 500px;
    border-radius: 10px;
  }
  
  .modal input,
  .modal textarea {
    width: 100%;
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 1.2rem;
  }
  
  .modal button[type="submit"] {
    background-color: #ff6f61;
    color: #fff;
    font-size: 1.6rem;
    padding: 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .modal button[type="submit"]:hover {
    background-color: #e15b50;
  }
  
  .close-modal {
    background: none;
    border: none;
    color: #333;
    font-size: 1.6rem;
    cursor: pointer;
  }
  
  /* Footer */
  .footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 20px;
  }
  