/* Hero Section */
.hero-section {
    background: url('https://source.unsplash.com/1600x900/?social-media') center/cover no-repeat;
    color: white;
    padding: 120px 20px;
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  
  .hero-title {
    font-size: 3rem;
    margin-bottom: 20px;
    font-weight: bold;
  }
  
  .hero-description {
    font-size: 1.5rem;
    margin-bottom: 40px;
  }
  
  .cta-button {
    background-color: #F9D423;
    color: #FF6F61;
    border: none;
    padding: 14px 30px;
    font-size: 1.2rem;
    cursor: pointer;
    border-radius: 50px;
    transition: 0.3s;
  }
  
  .cta-button:hover {
    background-color: #FF6F61;
    color: white;
  }
  
  /* Pulse Section */
  .pulse-section {
    background-color: #FF6F61;
    padding: 60px 20px;
    text-align: center;
    color: white;
  }
  
  .pulse-content h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .pulse-content p {
    font-size: 1.1rem;
    margin-bottom: 20px;
  }
  
  .pulse-animation {
    position: relative;
    display: inline-block;
  }
  
  .pulse {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #F9D423;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  /* Journey Section */
  .journey-section {
    background-color: #f7f7f7;
    padding: 60px 20px;
    text-align: center;
  }
  
  .journey-title {
    font-size: 2.5rem;
    margin-bottom: 30px;
  }
  
  .journey-timeline {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .timeline-item {
    background-color: white;
    padding: 30px;
    width: 80%;
    margin: 10px 0;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .timeline-item h3 {
    font-size: 1.8rem;
    margin-bottom: 15px;
  }
  
  .timeline-item p {
    font-size: 1.1rem;
  }
  
  /* Creative Section */
  .creative-section {
    padding: 60px 20px;
    text-align: center;
  }
  
  .creative-gallery {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .creative-item {
    width: 30%;
    padding: 20px;
    background-color: white;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .creative-item img {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .creative-item p {
    font-size: 1.1rem;
    margin-top: 10px;
  }
  
  /* Impact Section */
  .impact-section {
    background-color: #FF6F61;
    color: white;
    padding: 60px 20px;
    text-align: center;
  }
  
  .impact-card {
    background-color: #F9D423;
    margin: 10px;
    padding: 30px;
    width: 30%;
    display: inline-block;
    text-align: center;
    border-radius: 8px;
  }
  
  .impact-card h3 {
    font-size: 1.6rem;
    margin-bottom: 15px;
  }
  
  .impact-card p {
    font-size: 1.1rem;
  }
  
  /* Final CTA */
  .final-cta {
    text-align: center;
    padding: 60px 20px;
    background-color: #333;
    color: white;
  }
  
  .final-cta h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .final-cta p {
    font-size: 1.1rem;
    margin-bottom: 30px;
  }
  